import React from 'react';
import { withRouter } from 'react-router-dom';
import './WaitingRoom.css';



var shortTimePersons = [
  {
    animation1: {animation: 'stroll 12s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 11500
  }, 
  {
    animation1: {animation: 'stroll 3s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 2800
  }
]

var longTimePersons = [
  {
    animation1: {animation: 'stroll 6s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 5950
  }, 
  {
    animation1: {animation: 'stroll 9s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 8950
  },
  {
    animation1: {animation: 'stroll 20s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 19950
  },
  {
    animation1: {animation: 'stroll 30s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 28950
  },
  {
    animation1: {animation: 'stroll 12s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 11950
  }, 
  {
    animation1: {animation: 'stroll 3s linear'},
    animation2: {animation: 'walkanim 1s infinite steps(7)'},
    timeout: 2950
  }
]

class WaitingRoom extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      waitingRoomTimeStart: new Date(),
      isRedirect: true, 
      currentPerson: 0, 
      isShort: this.props.isShort,
      personsArray: this.props.isShort === true ? shortTimePersons : longTimePersons
    };
    this.routingFunction = this.routingFunction.bind(this);
    this.removePerson = this.removePerson.bind(this);
  }

  routingFunction = () => {
    var userData = {
      waitingRoomTime: Math.round((new Date() - this.state.waitingRoomTimeStart)/1000)
    }
    if (this.props.isRegular === true) {
      if (this.props.isShort === true) {
        this.props.history.push({pathname: '/r/s/selector', WaitRoomData: userData});
      }
      else {
        this.props.history.push({pathname: '/r/l/selector', WaitRoomData: userData});
      }
    }
    else {
      if (this.props.isShort === true) {
        this.props.history.push({pathname: '/v/s/selector', WaitRoomData: userData});
      }
      else {
        this.props.history.push({pathname: '/v/l/selector', WaitRoomData: userData});
      }
    }
  }


  removePerson() {
    var newPersons = this.state.currentPerson + 1
    console.log("removing " + this.state.currentPerson)
    if (newPersons === this.state.personsArray.length)
    {
      this.routingFunction();
    }
    else {
      alert("Click OK to progress in queue");
      this.setState({currentPerson: newPersons})
    }
    
  }

  drawPerson(currentPerson) {
    var personDescription = this.state.personsArray[currentPerson];
    //var numberStyle = currentPerson === 3 ? {color: 'Green'} : {color: 'Grey'}
    //var personDisplay = currentPerson === 3 ? 'You' : currentPerson;
    setTimeout(this.removePerson, personDescription.timeout)
    return (        
    <div id="walk-container" style={personDescription.animation1}>
      <div id="walk" style={personDescription.animation2}></div>
    </div>)
  }
  px
  render() {
    var isShort = this.state.isShort;
    return ( 
      <div>
        <div className="flex-content">
          <h2>You’ve just entered the Shlomo’s Kitchens store and joined the queue. Please wait
              until service starts.
          </h2>
          {this.state.currentPerson === 0 && isShort ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 1 && isShort ? this.drawPerson(this.state.currentPerson) : null}

          {this.state.currentPerson === 0 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 1 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 2 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 3 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 4 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          {this.state.currentPerson === 5 && isShort === false ? this.drawPerson(this.state.currentPerson) : null}
          { this.state.currentPerson < this.state.personsArray.length-1 ? <h2> <br></br><br></br> {this.state.personsArray.length - this.state.currentPerson - 1} more persons</h2> : <h2> <br></br><br></br> Service is starting...</h2>}
        </div>
      </div>
    )
  }
}

export default withRouter(WaitingRoom);