import React from 'react';
import Sidebar from './Sidebar';
import WaitingRoom from './WaitingRoom'
import './App.css';
import { createBrowserHistory } from "history";
import SelectorRoom from './SelectorRoom';
import Agree from './Agree';
import Story from './Story';
import Finish from './Finish'
import About from './About'
import Survey from './Survey'
import { Switch } from "react-router";

import { HashRouter, Route } from "react-router-dom";

const browserHistory = createBrowserHistory();

class App extends React.Component {
  constructor(props) {
    super(props);
  }

  render() { 
    return (
      <div className="App" id="outer-container">
        <HashRouter history={browserHistory} >
          <Sidebar pageWrapId={'page-wrap'} outerContainerId={'outer-container'} history = {browserHistory}/>
          <Route render = {({ location }) => (
              <Switch location = { location }>
                  <Route exact path="/r/s/" component={() => <Agree history = {browserHistory} isRegular={true} isShort={true}/>} />
                  <Route path="/r/s/story" component={() => <Story history = {browserHistory} isRegular={true} isShort={true}/>} />
                  <Route path="/r/s/wait_room" component={() => <WaitingRoom  history = {browserHistory} isRegular={true} isShort={true}/>} />
                  <Route path="/r/s/selector" component={() => <SelectorRoom isRegular={true} isShort={true}/> } />

                  <Route exact path="/r/l" component={() => <Agree history = {browserHistory} isRegular={true} isShort={false}/>} />
                  <Route path="/r/l/story" component={() => <Story history = {browserHistory} isRegular={true} isShort={false}/>} />
                  <Route path="/r/l/wait_room" component={() => <WaitingRoom  history = {browserHistory} isRegular={true} isShort={false}/>} />
                  <Route path="/r/l/selector" component={() => <SelectorRoom isRegular={true} isShort={false}/>} />

                  <Route exact path="/v/s/" component={() => <Agree history = {browserHistory} isRegular={false} isShort={true}/>} />
                  <Route path="/v/s/story" component={() => <Story history = {browserHistory} isRegular={false} isShort={true}/>}/>
                  <Route path="/v/s/wait_room" component={() => <WaitingRoom  history = {browserHistory} isRegular={false} isShort={true}/>}/>
                  <Route path="/v/s/selector" component={() => <SelectorRoom history = {browserHistory} isRegular={false} isShort={true}/>} />

                  <Route exact path="/v/l/" component={() => <Agree history = {browserHistory} isRegular={false} isShort={false}/>} />
                  <Route path="/v/l/story" component={() => <Story history = {browserHistory} isRegular={false} isShort={false}/>} />
                  <Route path="/v/l/wait_room" component={() => <WaitingRoom  history = {browserHistory} isRegular={false} isShort={false}/>} />
                  <Route path="/v/l/selector" component={() => <SelectorRoom history = {browserHistory} isRegular={false} isShort={false}/>} />
                  
                  <Route path="/complete_purchase" component={() => <Finish locationData = {location} isRegular={false}/>} />
                  <Route path="/about" component={() => <About/>} />
                  <Route path="/survey" component={() => <Survey/>} />
                </Switch>
          )} />
        </HashRouter>
      </div>
    );
  }
}

export default App;