import React from 'react';
import Gallery from 'react-grid-gallery';
import './Finish.css';
import './my-button.css';
import emailjs from 'emailjs-com';
import { withRouter } from 'react-router-dom';

function tileViewportStyleFn() { 
    return ({
            width: '175px',
            height: '220px',
            backgroundColor: 'white'
        });
}

function thumbnailStyleFn() { 
    var imgSrc = this.props.item.src;
    if (!imgSrc.includes('VIP')) {
        return ({
                width: '175px',
                height: '220px',
                backgroundColor: 'white',
                margin: '3px'
            });
    } 
    else {
        return ({
            width: '175px',
            height: '220px',
            backgroundColor: 'white',
            border: '8px solid transparent',
            borderImage: 'linear-gradient(to bottom, gold, rgba(0, 0, 0, 0)) 1 100%',
        });
    }
}

class Finish extends React.Component{
    constructor (props) {
      super(props)
      this.state = {
        showMore: false,
        finishDate: this.props.locationData.pData.finishDate,
        userItems: Array.from(this.props.locationData.pData.userItems),
        totalPrice: this.props.locationData.pData.totalPrice,
        budget: this.props.locationData.pData.budget,
        isRegular: this.props.locationData.pData.isRegular,
        isShort: this.props.locationData.pData.isShort,
        clickedRegularViewMore: this.props.locationData.pData.clickedRegularViewMore,
        clickedVipViewMore: this.props.locationData.pData.clickedVipViewMore,
        history: this.props.locationData.pData.history,
        startDate: this.props.locationData.pData.startDate,
        waitigRoomTime: this.props.locationData.pData.waitigRoomTime,
      }
      this.routingFunction = this.routingFunction.bind(this);
      this.sendReportToEmail = this.sendReportToEmail.bind(this);
      this.backToStore = this.backToStore.bind(this);
    }

    sendReportToEmail() {
        var calcTotalTime = Math.round((new Date() - this.state.startDate)/1000);
        var templateParams = {
            finishDate: this.state.finishDate,
            totalPrice: this.state.totalPrice,
            budget: this.state.budget,
            from_name: 'Experiment',
            isRegular: this.state.isRegular,
            clickedRegularViewMore: this.state.clickedRegularViewMore,
            clickedVipViewMore: this.state.clickedVipViewMore,
            totalTime: calcTotalTime,
            waitingRoomTime: this.state.waitigRoomTime,
        };
        var theObject = this;
        emailjs.send('service_8ww919p', 'template_h1x55nb', templateParams, 'user_KnDPo0mi8tW6gzapNbVeb')
            .then(function(response) {
               console.log('SUCCESS!', response.status, response.text);
               theObject.routingFunction();
            }, function(error) {
               console.log('FAILED...', error);
               alert("Something went wrong... Please try again in a few minutes.");
            });
    }

      backToStore = () => {
        var userData = {
            oldState: this.props.locationData.pData.oldState,
        }

        if (this.state.isRegular === true) {
          if (this.state.isShort === true) {
            this.props.history.push({pathname: '/r/s/selector', backData: userData});
          }
          else {
            this.props.history.push({pathname: '/r/l/selector', backData: userData});
          }
        }
        else {
          if (this.state.isShort === true) {
            this.props.history.push({pathname: '/v/s/selector', backData: userData});
          }
          else {
            this.props.history.push({pathname: '/v/l/selector', backData: userData});
          }
        }
      }


    routingFunction = () => {
          this.state.history.push({
              pathname: '/survey'
          });
      }
    
    render() {
        return (
            <div className="finish-flex-content">
                <h2>Greet! Almost done</h2>
                <h3>Finish Date {this.state.finishDate}</h3>
                <div className="finish-gallery">
                        <Gallery
                            tileViewportStyle={tileViewportStyleFn}
                            thumbnailStyle={thumbnailStyleFn}
                            enableLightbox={true}
                            images={this.state.userItems}
                            enableImageSelection={false}
                            margin={7}
                        />                
                </div>
                <label style={{"color": "green"}}>{"Budget: "+ this.state.budget.toFixed(2) + " $"}<br></br></label>
                <label style={{"color": "red"}}>{"Total: " + this.state.totalPrice.toFixed(2) + " $"} <br></br><br></br></label>   
                <button className="my-button"  onClick={this.sendReportToEmail}>Complete service and proceed to survey</button>
                <label> Or</label>
                <button className="my-button"  onClick={this.backToStore}>Return to store</button>
            </div>
        );
    }
}

export default withRouter(Finish);