import React from 'react';
import './Story.css';
import { withRouter } from "react-router";
import './my-button.css'

class Story extends React.Component{
    constructor (props) {
      super(props)
      this.state = {
        isAgree: false
      }
    }

    handleClick = () => {
      if (this.props.isRegular === true) {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/r/s/wait_room'});
        }
        else {
          this.props.history.push({pathname: '/r/l/wait_room'});
        }
      }
      else {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/v/s/wait_room'});
        }
        else {
          this.props.history.push({pathname: '/v/l/wait_room'});
        }
      }
    }

    render(){
      var isRegular = this.props.isRegular;
      return (
       <div className="story-flex-content">
        <div className="story-flex-content-text">
            {isRegular === true ? 
              <h3 style={{textAlign: "left"}}>
                You and your partner recently returned from your honeymoon (that was pretty nice
                by the way) and you are going to move into your new apartment. <br></br>So firstly, you
                decided to purchase some basic items for your new kitchen such as fridge, worktop
                surface and stove.
                On advice of your friends, Ross and Rachel, you decided to buy at Shlomo’s
                Kitchens, though you have never been in this store.
                You have a budget of $4300 for all 3 items together.
                At each step you will choose a product you like most of all that would nicely fit your
                new kitchen and budget as well. At each step you can view additional attractive
                options by clicking on “View more” button for a 2 second fee.
                You very want to make all the items look perfectly together in your new dream
                kitchen.
                So, let’s start!
              </h3> 
              : 
              <h3 style={{textAlign: "left"}}>
                You are an interior designer who specializes in kitchen styling. One of your favorite
                furniture suppliers is Shlomo’s Kitchens store. You get along with its owner, Shlomo.
                He usually makes special prices for you, so you are a regular customer of his store.
                You’ve got a new order from your new customer, so you need to purchase 3 items at
                Shlomo’s store. The items are fridge, worktop surface and stove.
                You have a budget of $4300 for all 3 items together.
                At each step you will choose a product you like most of all that would nicely fit your
                new kitchen and budget as well. At each step you can view additional attractive
                options by clicking on the “View more” button for a 2 second fee.
                You want to amaze your new customer very much because you want them
                contacting you next time.
                So, let’s start!
              </h3> 
              }
            <br></br>
            
          </div>
          <button className="my-button" onClick={()=> this.handleClick()}>Lets start</button>
       </div>
      );
    }
  }
  
  export default withRouter(Story);