import React from 'react';
import './Agree.css';
import './my-button.css'
import { withRouter } from "react-router";

class Agree extends React.Component{
    constructor (props) {
      super(props)
      this.state = {
        isAgree: false
      }
    }

    handleClick = () => {
      if (this.props.isRegular === true) {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/r/s/story'});
        }
        else {
          this.props.history.push({pathname: '/r/l/story'});
        }
      }
      else {
        if (this.props.isShort === true) {
          this.props.history.push({pathname: '/v/s/story'});
        }
        else {
          this.props.history.push({pathname: '/v/l/story'});
        }
      }
    }

    handleAgreeCheck() {
        this.setState({isAgree: !this.state.isAgree})
    }

    render(){
      return (
       <div className="agree-flex-content">
         <div className="agree-flex-content-text">
            <h2>Hi, dear friend!</h2>
            <br></br>
            <br></br>
              <h3 style={{textAlign: "left"}}>
                Thank you for joining our experiment!<br></br>
                We are going to simulate the Kitchen Design Service at Shlomo’s Kitchens store.
                In the next step you will be provided a background story that will tell you how you
                came to Shlomo’s Kitchens. You will ask to read it precisely and pass all the steps of
                service (including waiting time in the queue at the beginning) and answer a little
                survey in the end.<br></br>
                During the waiting time in the virtual queue every time the queue length reduces by 1
                man you will ask to press “OK” in order to move forward in line.<br></br>
                All information you provide will remain confidential and will not be associated with
                your name.
                No money and no items at the store will be exchanged.
                If you agree to the terms of the experiment, please check the box below:
            </h3>

              <input type="checkbox" id="agree_check" checked={this.state.isAgree} onChange={() => this.handleAgreeCheck()}/>
              <label> Yes, I agree.</label>
              <br></br>
              <button className="my-button" disabled={!this.state.isAgree} onClick={()=> this.handleClick()}>Next</button>

            
            
          </div>
       </div>
      );
    }
  }
  
  export default withRouter(Agree);