import React from 'react';
import { slide as Menu } from 'react-burger-menu';
import './Sidebar.css';

var buildNumber = null;

export default props => {
  if (buildNumber === null) {
    var buildNumberMeta = document.getElementsByTagName('meta')["ver"];
    if (buildNumberMeta != undefined)
    {
      buildNumber = buildNumberMeta.getAttribute("build-version");
    }
  }
  return (
    <Menu>
      <a className="menu-item" href="/tech-service/#/about">
        About
      </a>
      {buildNumber !== null ? <label style={{fontSize: '14px'}}>Build: {buildNumber}</label> : null }
    </Menu>
  );
};